import React, { Component } from 'react';
import './Inqstyles.css';

class I002 extends Component {
  
  renderEssay = () => {
    return (
      <div className="appear grid-break">
        <h3>
        <i>
        In order to start a meaningful conversation about sustainability, we need to explore more precise frameworks of study; in this essay, we dig into the nature and limitations of language, and how it affects discourse on complex topics.
        </i>
        <br/><br/>
        One of the biggest problems with the word 'sustainability' is the unique internalized definition that each individual person holds. Two people can talk broadly about cities, cars, energy, and environmental policy without ever really touching on a core understanding of which solutions might be best. What are we really talking about? Sustainability is so hard to pin downand so embedded in progressive philosophythat it deserves some analysis to bring its true meaning to light.
        <br/><br/>
        Two major treatments on meaning and language come to mind: one of them would be Immanuel Kants Critique of Pure Reason (1781), and the other lies, well, somewhere in the depths of mid-twentieth century Post-Structuralism. There is a tension between the two systems that well discuss here, but they are also two sides of the same coinand both are indispensable to having any real discussion about sustainability on local or world stages.
        <br/><br/>
        <b>part one: noumena</b>
        <br/><br/>
        Immanuel Kant coined the term 'noumenon' to describe the concepts that represent and categorize the ideas we derived from the world. For instance: according to Kant, we cant possibly describe every dog on Earth with a single word, but we try anyway. We use the word 'dog' when were talking about some subset of the Canine family, and most people get the picture, because dogs are pretty common. If a dog walked into your house and you were interacting with it, Kant would saythat dogin particular, was a phenomenon, an actual instance, of the noumenon 'dog.'
        <br/><br/>
        So phenomena are the actual instances of things, noumena are the high-level concepts describing them. They're very often the same word, as it was in the case just described. We rely on additional language to instantiate a phenomenon from the noumenon from which it is derived. If someone asked you: Hey uh, who came over to your house today?, and you replied: Dog, youd sound nuttymostly because English sounds ridiculous without articlesbut also because you tried to use a general, high-level concept (a noumenon) to describe a real-life phenomenon. If the other person wasnt there to experience the exact same phenomenon you did, we have to use details and all sorts of explanatory language to elaborate.
        <br/><br/>
        <b>phenomenon;</b> the "thing-as-it-appears," that frisky Dachshund, Franky, who entered your home somehow, who you are petting right now
        <br/><br/>
        <b>noumenon;</b> the "object-in-itself," the concept 'dog', attempting to describe all dogs, from every universe and from every possible timeline.
        <br/><br/>
        Why is this important? Well, because patriarchy, racism, fake news, fascism, and of course, sustainability, are all noumena. We have no perfect collective experience of these concepts, and languageaccording to Kantis fundamentally incapable of capturing the true essence of all the phenomena these words are trying to describe. Human experience is but a sliver of reality; language is but a sliver of all possible meaning. The noumenon is unknowable. I might have seen a Norwegian Lundehund one time, but you've only ever experienced traditional American breeds; since our subjective experiences color our understandings differently, neither of us have a complete or even parallel understanding of what that noumenon truly represents.
        <br/><br/>
        Much of the time, people are using the most controversial noumena differently as a function of their own experience. In doing so, they fail to communicate their true intentthe pure, platonic form of the word. Those who haved experienced something like racism many times over, have a much more complete understanding of what it looks like; those who haven't struggle to integrate it into their worldview. We live in a world where ignorance appears more rampant, undoubtedly because noumena are moving so freely. People are often right and wrong at the same time.
        <br/><br/>
        With regard to sustainability: its a personal least-favorite word of mine, not least for its awkward affixes and general inelegance. That said, it seems our own internal versions of 'sustainability' tend to merge with the popular, simplified conception of it. Society at large seems unwilling to acknowledge what a deceptively complex idea it is, and as such the term loses value.
        <br/><br/>    
        <b>part two: post-structuralism</b>
        <br/><br/>
        Almost two hundreds years later, in the early 1900s, philosophers started looking more closely at language and meaning in the context of Structuralism, which discards the idea of universal platonic concepts, and says that we really only understand the meaning of words as they relate to other words. No concept is original; they merely inhabit pre-existing structures. We cant understand the concept of 'democracy' without building onto the structure of the word 'government,' which in turn is built onto so much extant scaffolding: bureaucracy, taxation, collective decision-making, etc.
        <br/><br/>
        Some decades later, the French Post-Structuralists of the 1960s came along and deconstructed Structuralism itself. A good explanation comes from Corey Mohler:
        <i>
        Post-structuralism is an internal, structuralist critique of structuralism itself. [Derrida] claims that even the structure of the words is not adequate to understand the meaning of a word, since the meaning of a word always depends on its context, both current and historical, and that context is never stable. Therefore it is impossible for words to have fixed meanings or be understood completely.
        </i>
        <br/><br/>
        Post-structuralism, therefore, injects new layers into our collective conception of 'sustainability.' The older proxies we once used to signal our concern for the earthrainforest destruction, the 'hole in the ozone,'' acid rain, global warminghave generally been discarded in favor of 'climate change.' In a post-structuralist world, we encounter the collision of context, connotation, power, hierarchy, and even enunciation when we try to agree on new ideas and their place in society.
        <br/><br/>
        The material landscape is changing too: Tesla is arguably the most powerful private company ever to take the reigns on reducing emissions; Democrats and Republicans are polarized on a massive 'Green New Deal.' We now view sustainability almost solely in the context of corporate production and federal policy. When the potential of alternative systems and individual action are reduced to nothing, conversation on the whole is reduced to overt simplifications and virtue signaling. Likewise, there is no marginal value in deeper investigation; I can encourage a friend to read McDonoughs <i>Cradle to Cradle</i>, but no ones believes it's worth the effort to chug through The Journal of Industrial Ecology every month.    
        <br/><br/>
        <b>part three: a sustainable definition</b>
        <br/><br/>
        In 2019, it feels as if no one has much to say about sustainability beyond making sure the Earth doesnt get a few degrees hotter, at all costs but certainlya number of intellectuals have made an attempt at defining the term. The most famous, in academic circles, was offered in 1987 by the Brundtland Commission, a committee appointed by the UN as a means of rallying countries together on the issue:
        <br/><br/>
        <q>
        "Development that meets the needs of the present without compromising the ability of future generations to meet their own needs."
        </q>
        <br/><br/>
        I find a number of problems with this definition, not least that it reduces sustainability to a human-centric resource allocation problem. Also: future generations? How many? Are we talking about the next fifty generations or the next fifty years? Time is left open-ended, and every following measure of sustainability breaks down. Should we be striving to create sustainable Earthly systems until the Sun explodes? If so, what are the real barriers and goals associated with humanity meeting its own basic needs until solar collapse?
        <br/><br/>
        While agreeing on a definition is half the battlethe other half really is figuring what to do about it. Sustainability is now popularly conceived as a problem of government policy, and while indeed, it is a problem of collective-decision making, coercion, analysis, free-riders, globalism, and old-fashioned public debate, there is also so much more potential for innovation. Stay tuned.
        </h3>
      </div>
    )
  }

  render() { 
    return (
      <div>
          {this.renderEssay()}
      </div>
    );
  }
}

export default I002;